.add-form{
    position: fixed;
    top: 5rem;
    background-color: orangered;
    padding: 2rem 3rem;
    border-radius: 10px;
    color: white;
}
form{
    width: 100%;
}
.add-form label{
    /* width: 80px; */
    /* background: black; */
    font-size: 20px;
}
input{
    width: 100%;
    margin-bottom: 15px;
    padding: 10px;
}
.select{
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
}

button{
    background-color: white;
    color: orangered;
    outline: none;
    border: none;
    padding: 10px 20px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}